import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Login from './components/Login/Login';
import PrivateRoute from './components/PrivateRoute/PrivateRoute';
import Home from './pages/Home/Home';
import Profile from './pages/Profile/Profile';
import Register from './components/Register/Register';
import Header from './components/Header/Header';
import './App.css';
import toastr from 'toastr';
import toastrSetup from './helpers/toastrSettings';
import ForgotPassword from './components/ForgotPassword/ForgotPassword';
import ChangePassword from './components/ChangePassword/ChangePassword';
import auth, { checkAuth } from './helpers/auth';
import ClipLoader from 'react-spinners/ClipLoader';

const App = () => {
  toastr.options = toastrSetup;

  const [loggedUser, setLoggedUser] = useState(null);

  useEffect(() => {
      const fetchAuth = async() => {
          if(localStorage.getItem('userToken') === null) return setLoggedUser({ username: 'Guest' });
              else {
                  const token = localStorage.getItem('userToken');
                  const res = await checkAuth(token);
                  if(res.status === 1) {
                      auth.authenticate(res.username);
                      setLoggedUser({ username: res.username }); 
                  } else setLoggedUser({ username: 'Guest' }); 
              }
      }
      fetchAuth();
  }, []);

  if(loggedUser === null) return <div className="loading"><ClipLoader size={50} color={'#485877'} /></div>;

  return (
    <Router basename="/fullauth">
      <div>
        <Header/>
        <Route exact path="/" component={Home}/>
        <Route path="/login" component={Login} />
        <Route path="/register" component={Register} />
        <Route path="/recover" component={ForgotPassword} />
        <Route path="/changepass" component={ChangePassword} />
        <PrivateRoute path='/profile' component={Profile} />
      </div>
    </Router>
  )
}

export default App;