import React from 'react';
import { withRouter } from 'react-router-dom';
import auth from '../../../helpers/auth';
import classes from './AuthStatus.module.css';

const AuthStatus = withRouter(({ history }) => {
  let component = null;

  const handleSignOut = () => {
    auth.signout();
    localStorage.removeItem('userToken');
    history.push('/');
  }

  if(auth.isAuthenticated) {
    const width = window.innerWidth || document.documentElement.clientWidth|| document.body.clientWidth;
    const userUsername = width <= 510 ? '' : `Welcome ${auth.username}!`;
    component = 
      <div className={classes['sign-in']}>
        { userUsername } <button onClick={handleSignOut}><i className="fas fa-sign-out-alt"></i> Sign out</button>
      </div>;
  } else {
    component = 
      <div className={classes['sign-out']}>
        <button onClick={() => history.push('/register')}>Sign up</button>
        <button onClick={() => history.push('/login')}><i className="fas fa-sign-in-alt"></i> Login</button>
      </div>;
  }

  return (
    <div className={classes.AuthStatus}>{component}</div>
  )
})

export default AuthStatus;