import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import auth, { checkAuth } from '../../helpers/auth';
import ClipLoader from 'react-spinners/ClipLoader';
import classes from './Profile.module.css';

const Profile = props => {
    const history = useHistory();

    const [loggedUser, setLoggedUser] = useState(null);
    
    useEffect(() => {
        const fetchAuth = async() => {
            if(localStorage.getItem('userToken') === null) return history.push('/login', { from: { pathname: history.location.pathname } });
                else {
                    const token = localStorage.getItem('userToken');
                    const res = await checkAuth(token);
                    if(res.status === 1) {
                        auth.authenticate(res.username);
                        setLoggedUser({ username: res.username }); 
                    } else return history.push('/login', { from: { pathname: history.location.pathname } });
                }
        }

        fetchAuth();
    }, [history]);
    
    if(loggedUser === null) return <div className={classes.Loading}><ClipLoader size={50} color={'#485877'} /></div>;
    
    return(
        <div>Hello {loggedUser.username}</div>
    );
}

export default Profile;
